
body {
  margin: 0;
  background: #f0efef;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.Pokedex {
  text-align: center;
  margin: 20px auto;
}

.Pokedex-Winning {
  margin-top: 0;
  color: rgb(22, 172, 22);
}

.Pokedex-Losing {
  margin-top: 0;
  color: rgb(214, 33, 169);
}
.Pokecard {
  background: #ffffff;
  width: 250px;
  text-align: center;
  border-radius: 1px;
  padding: 5px;
  display: inline-block;
  margin: 20px 50px;
  box-shadow: 5px 5px rgb(165, 164, 164);
  -webkit-animation: come-in 2s ease;
          animation: come-in 2s ease;
}

.Pokecard:nth-child(odd) {
  -webkit-animation-duration: 1.6s;
          animation-duration: 1.6s;
}

.Pokecard-img-div {
  text-align: center;
  margin: 2px auto;
  width: 246px;
  height: 240px;
  background: #f0efef;
}

.Pokecard-img {
  margin: 0 auto;
  -webkit-transition-duration: 2s, 2s;
          transition-duration: 2s, 2s;
}

.Pokecard-img:hover {
  -webkit-transition-duration: 2s, 2s;
          transition-duration: 2s, 2s;
  -webkit-transform: scale(1.5,1.5);
          transform: scale(1.5,1.5);
}

.Pokecard-p {
  margin: 0;
}

@-webkit-keyframes come-in {
  from { -webkit-transform: translateY(-150%); transform: translateY(-150%) } to { -webkit-transform: translateY(0); transform: translateY(0); }
}

@keyframes come-in {
  from { -webkit-transform: translateY(-150%); transform: translateY(-150%) } to { -webkit-transform: translateY(0); transform: translateY(0); }
}

